export async function http(url: string, forward: string, headers: any, params: any) {
  return new Promise(async (resolve, reject) => {
    try {
      if (forward) {
        url = `${forward}${url}`;
        delete params.encryptData;
      } else {
        url = `https://qiyema-api-v2.alipay.com${url}`;
      }
      // 默认json formdata类型浏览器会自动设置为 multipart/form-data，并添加边界（boundary）
      //   let headers = {};
      if (!(params instanceof FormData)) {
        headers['Content-Type'] = 'application/json';

        params = JSON.stringify(params);
      }

      const response = await fetch(url, {
        method: 'POST',
        headers,
        body: params,
      });
      const responseData = await response.json();
      console.log('responseData', responseData);

      if (response.ok) {
        if (responseData.success) {
          resolve(responseData);
        } else {
          reject(responseData);
        }
      } else {
        reject(responseData);
      }
    } catch (error) {
      reject(error);
    }
  });
}
