/* src/components/my-message/my-message.css */
.message {
  display: flex;
  align-items: center;
  padding: 12px 16px;
  border-radius: 4px;
  box-shadow: 0 4px 12px rgba(000, 000, 000, 0.15);
  font-size: 14px;
  background: #fff;
  color: #333;
  position: absolute;
  top: 90px;
  left: 50%;
  transform: translateX(-50%);
  transform: translateY(0);
  z-index: 9999;
  animation: fadeIn 0.3s ease-in-out;
}

.icon {
  margin-right: 8px;
  font-size: 16px;
  width: 16px;
  height: 16px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
