:host {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1000;
}

:host(.visible) {
  display: block;
}

.invoice-selector-mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.45);
  z-index: 1000;
}

.invoice-selector-drawer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 80%;
  max-width: 900px;
  background: #fff;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.15);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 1001;
  display: flex;
  flex-direction: column;
}

:host(.visible) .invoice-selector-drawer {
  transform: translateX(0);
}

.invoice-selector-header {
  position: relative;
  padding: 23px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  display: flex;
  align-items: center;
  gap: 16px;
}

.header-title h2 {
  margin: 0;
  font-size: 16px;
  font-weight: 500;
}

.efficiency-tip {
  box-sizing: border-box;
  padding-left: 74px;
  width: 314px;
  height: 26px;
  line-height: 26px;
  background-image: url('/img/efficiency-tip-bg.png');
  background-size: 100% 100%;
  text-decoration: none;
  font-size: 12px;
  color: #333333;
  cursor: not-allowed;
}

.efficiency-tip span {
  color: #1f7cff;
}

.header-close {
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translate(0, -50%);
  padding: 10px;
  cursor: pointer;
  font-size: 20px;
  color: #999;
}

.invoice-table-con {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.invoice-selector-actions {
  padding: 26px 24px 16px 24px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.upload-button {
  width: 88px;
  height: 32px;
  background-image: url('/img/upload-button-bg.png');
  font-size: 14px;
  color: #ffffff;
  line-height: 32px;
  text-align: center;
  cursor: pointer;
}

.tab-switcher {
  display: flex;
  box-sizing: border-box;
  padding: 4px;
  width: 153px;
  height: 36px;
  background-color: #eef4fe;
  border-radius: 19px;
}

.tab-button {
  width: 72px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  background-color: #eef4fe;
  color: #36373d;
  border-radius: 14px;
  cursor: pointer;
}

.tab-button.active {
  background: #ffffff;
  color: #005bf8;
}

.invoice-table {
  flex: 1;
  overflow-y: auto;
  padding: 0 24px;
  padding-bottom: 200px;
}

.table-header {
  display: grid;
  grid-template-columns: 40px 100px 1fr 120px 1fr 100px 80px;
  background: #fafafa;
  color: #36373d;
  padding: 18px 0;
  position: sticky;
  top: 0;
  z-index: 1;
  font-size: 12px;
  line-height: 18px;
}

.table-row {
  display: grid;
  align-items: center;
  grid-template-columns: 40px 100px 1fr 120px 1fr 100px 80px;
  border-bottom: 1px solid #f0f0f0;
  padding: 20px 0;
  position: relative;
}

.table-row:hover .not-reimbursed-tips {
  display: block;
}

.table-row:hover .reimbursed-tips {
  display: block;
}

.not-reimbursed-tips {
  position: absolute;
  top: -30px;
  left: 0px;
  z-index: 1;
  width: 167px;
  height: 62px;
  display: none;
}

.reimbursed-tips {
  position: absolute;
  top: 52%;
  left: 26px;
  z-index: 1;
  transform: translate(0, -50%);
  width: 160px;
  height: 56px;
  display: none;
}

.selected {
  background: #eef4fe;
}

.linked {
  background: #fffbf7;
}

.table-header .table-cell {
  font-size: 12px;
  color: #36373d;
  font-weight: 500;
}

.table-cell {
  padding: 0 8px;
  display: flex;
  align-items: center;
  color: #36373d;
  font-size: 12px;
  line-height: 14px;
}

.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.selected-icon {
  width: 16px;
  height: 16px;
}

.relation-icon {
  width: 16px;
  height: 16px;
}

.checkbox-cell {
  justify-content: center;
  height: 16px;
}

.checkbox-cell input {
  width: 16px;
  height: 16px;
}

.view-link {
  color: #1890ff;
  text-decoration: none;
  cursor: pointer;
}

/* 分页 */
.pagination-con {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 16px 24px;
  padding-right: 0;
  color: #36373d;
}

.left-con,
.right-con {
  cursor: pointer;
  padding: 8px;
  width: 10px;
  height: 10px;
}
.current-page-con {
  padding: 3px 10px;
  border: 1px solid #005bf8;
  border-radius: 4px;
  color: #005bf8;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
}

.slash {
  margin: 0 4px;
  width: 5px;
  height: 10px;
}

.total-page-con {
  padding: 3px 10px;
  color: #8e92a2;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
}

.gary {
  opacity: 0.6;
}

/* 底部确定 */
.invoice-selector-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 30px 24px;
  font-size: 14px;
  line-height: 20px;
  border-top: 1px solid #f0f0f0;
  background: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-actions {
  display: flex;
  gap: 8px;
}

.cancel-button {
  padding: 5px 16px;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  border-radius: 100px;
  cursor: pointer;

  border: 1px solid #d5d8de;
  background: white;
  color: #36373d;
}

.confirm-button {
  padding: 5px 16px;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  border-radius: 100px;
  cursor: pointer;
  border: 1px solid #005bf8;
  background: #005bf8;
  color: #fff;
}

.error-con {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.error-message {
  margin-top: 50px;
  font-size: 18px;
  line-height: 25px;
  color: #8b8ea0;
  text-align: center;
}

.error-refresh {
  cursor: pointer;
  margin-top: 24px;
  padding: 5px 16px;
  background: #005bf8;
  font-size: 14px;
  line-height: 22px;
  color: #ffffff;
  line-height: 22px;
  border-radius: 100px;
}

.empty-state {
  margin-top: 72px;
  text-align: center;
}

.empty-message {
  margin-top: 20px;
  font-size: 14px;
  line-height: 20px;
  color: #8b8ea0;
  text-align: center;
}
