import { Component, Host, h, Prop, Method, Event, EventEmitter, State } from '@stencil/core';

const backIcon = '/img/back-icon.png';

export interface InvoiceDetailData {
  invoiceNo: string;
  invoiceDate: string;
  payeeName: string;
  invoiceAmount: number;
  payerName: string;

  billImages?: Array<string>;
}

@Component({
  tag: 'invoice-detail',
  styleUrl: 'invoice-detail.css',
  shadow: true,
})
export class InvoiceDetail {
  @Prop({ mutable: true, reflect: true }) visible: boolean = false;
  @Prop() invoiceData: InvoiceDetailData;
  @Event() closeDetail: EventEmitter<void>;
  @State() showBillDetail: boolean = false;
  @State() selectedImageUrl: string = '';

  /**
   * Close the invoice detail drawer
   */
  @Method()
  async close() {
    this.closeDetail.emit();
  }

  disconnectedCallback() {
    document.body.style.overflow = '';
  }

  render() {
    if (!this.visible || !this.invoiceData) {
      return null;
    }

    return (
      <Host class={this.visible ? 'visible' : ''}>
        <div class="invoice-detail-drawer">
          <div class="invoice-detail-header">
            <div class="header-title">
              <img class="back-icon" src={backIcon} onClick={() => this.close()} />
              <span class="back-title">发票详情</span>
            </div>
            <div class="header-close" onClick={() => this.close()}>
              <span>×</span>
            </div>
          </div>

          <div class="invoice-detail-content">
            <div class="invoice-image-con">
              <img class="invoice-image" src="https://mass.stable.alipay.net/induspt_invoice/afts/img/A*6e4ZR4SU8p0AAAAAAAAAAAAAAUYnAQ"></img>
            </div>

            <div class="detail-section">
              <h3>基本信息</h3>
              <div class="detail-grid">
                <div class="detail-item">
                  <label>商家名称</label>
                  <div>{this.invoiceData.payerName}</div>
                </div>
                <div class="detail-item">
                  <label>开票时间</label>
                  <div>{this.invoiceData.invoiceDate}</div>
                </div>
                <div class="detail-item">
                  <label>发票金额</label>
                  <div>{this.invoiceData.invoiceAmount}</div>
                </div>
                <div class="detail-item">
                  <label>发票号码</label>
                  <div>{this.invoiceData.invoiceNo}</div>
                </div>

                <div class="detail-item">
                  <label>更多备注</label>
                  <div>-</div>
                </div>

                <div class="detail-item">
                  <label>店家账单</label>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
