.invoice-detail-drawer {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  width: 80%;
  max-width: 900px;
  background: #fff;
  box-shadow: -2px 0 8px rgba(0, 0, 0, 0.15);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  z-index: 1001;
  display: flex;
  flex-direction: column;
}

:host(.visible) .invoice-detail-drawer {
  transform: translateX(0);
}

.invoice-detail-header {
  padding: 24px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-title {
  display: flex;
  align-items: center;
}

.header-title .back-icon {
  width: 6px;
  height: 12px;
  padding: 8px;
  padding-left: 0;
  cursor: pointer;
}

.header-title .back-title {
  color: #36373d;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  cursor: pointer;
}

.invoice-status {
  padding: 2px 8px;
  background: #f6ffed;
  border: 1px solid #b7eb8f;
  border-radius: 2px;
  color: #52c41a;
  font-size: 12px;
}

.header-close {
  cursor: pointer;
  font-size: 20px;
  color: #00000073;
  
}

.invoice-detail-content {
}

.invoice-image-con {
  box-sizing: border-box;
  margin: 0 24px;
  margin-top: 12px;
  padding: 24px 53px;
  background: #fafcff;
  border-radius: 16px;
}

.invoice-image {
  max-width: 100%;
  max-height: 465px;
  border-radius: 14px;
  box-shadow: 0px 5px 21px 0px #003e8f0d;
}

.detail-section {
  box-sizing: border-box;
  margin: 0 24px;
  margin-top: 8px;
  padding: 24px;
  background: #fafcff;
  border-radius: 16px;
}

.detail-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 24px;
}

.detail-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.detail-item label {
  font-size: 14px;
  line-height: 20px;
  color: #8b8ea0;
}

.detail-item div {
  font-size: 14px;
  line-height: 20px;
  color: #36373d;
}

.invoice-detail-footer {
  padding: 24px;
  border-top: 1px solid #f0f0f0;
  display: flex;
  justify-content: flex-end;
}

.bill-image {
  max-width: 44px;
  max-height: 44px;
  margin-left: 8px;
}

.no-images {
  color: #999;
  font-size: 14px;
  padding: 8px 0;
}

.download-button {
  padding: 5px 16px;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  border-radius: 100px;
  cursor: pointer;
  border: 1px solid #005bf8;
  background: #005bf8;
  color: #fff;
}

.bill-images-preview {
  display: flex;
  gap: 8px;
  align-items: center;
  cursor: pointer;
}

.bill-images-preview .bill-image {
  width: 44px;
  height: 44px;
  object-fit: cover;
  border-radius: 4px;
}

.bill-images-preview .more-images {
  width: 44px;
  height: 44px;
  background: #f5f5f5;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #666;
  font-size: 14px;
}
