// src/components/my-message/my-message.tsx
import { Component, Prop, h } from '@stencil/core';

const successIcon = '/img/success-icon.png';
const errorMessageIcon = '/img/error-message-icon.png';

@Component({
  tag: 'my-message',
  styleUrl: 'my-message.css',
  shadow: true,
})
export class MyMessage {
  @Prop() type: 'success' | 'error' | '' = '';
  @Prop() message: string = '';

  // @State() isVisible = false;

  componentWillLoad() {
    // this.isVisible = true;
    // setTimeout(() => {
    //   this.hide();
    // }, 3000);
  }

  hide() {
    // this.isVisible = false;
  }

  render() {
    // if (!this.isVisible) return null;

    let icon = '';
    switch (this.type) {
      case 'success':
        icon = successIcon;
        break;
      case 'error':
        icon = errorMessageIcon;
        break;
      // case 'warning':
      //   icon = '⚠️';
      //   break;
      // default:
      //   icon = 'ℹ️';
    }

    return (
      <div class={`message ${this.type}`}>
        {icon && <img class="icon" src={icon}></img>}
        <span class="content">{this.message}</span>
      </div>
    );
  }
}
