import { Component, Host, h, State, Prop, Method, Event, EventEmitter, Watch, Element } from '@stencil/core';
import { InvoiceDetailData } from '../invoice-detail/invoice-detail';
import api from '../../utils/api';
import { http } from '../../utils/http';
import { objectToFormData, safeParse } from '../../utils/utils';
import { InvoiceTypeEnum, SelectStatusEnum, ReimburseStatusEnum } from '../../utils/enum';

// 修改图片导入方式
const errorIcon = '/img/error-icon.png';
const selectedIcon = '/img/selected-icon.png';
const unselectedIcon = '/img/unselected-icon.png';
const emptyIcon = '/img/empty-icon.png';
const relationIcon = '/img/relation-icon.png';
const notReimbursedTips = '/img/not-reimbursed-tips.png';
const reimbursedTips = '/img/reimbursed-tips.png';
const leftArrow = '/img/left-arrow.png';
const slash = '/img/slash.png';
const rightArrow = '/img/right-arrow.png';

interface ApiResponse<T> {
  success: boolean;
  data: {
    items?: T;
    totalNum?: number;
    emailAddress?: string;
  };
  message: string;
}

export interface InvoiceItem {
  invoiceNo: string;
  invoiceDate: string;
  payeeName: string;
  invoiceAmount: number;
  payerName: string;
  invoiceLabel: string;
  reimburseStatus: string;
  linked: boolean;
  selected: boolean;
  billImages?: Array<string>;
}

@Component({
  tag: 'invoice-selector',
  styleUrl: 'invoice-selector.css',
  shadow: true,
})
export class InvoiceSelector {
  @Element() el!: HTMLElement; // 获取组件根元素

  @Prop({ mutable: true, reflect: true }) visible: boolean = false;
  @Prop({ mutable: true, reflect: true }) multiple: boolean = false;
  @Prop({ mutable: true, reflect: true }) forward: string = '';
  @Prop({ mutable: true, reflect: true }) token: string = '';
  @Prop({ mutable: true, reflect: true }) appid: string = '';
  @Prop({ mutable: true, reflect: true }) customheader: string = '';
  @Prop({ mutable: true, reflect: true }) customqueryparam: string = '';

  @Event() onConfirm: EventEmitter<InvoiceItem[]>;
  @Event() onCancel: EventEmitter<void>;
  @Event() onError: EventEmitter<Error>;

  @State() isError: boolean = false;
  @State() showLoading: boolean = false;
  @State() messageSetting: { type: '' | 'success' | 'error'; message: string } = {
    type: '',
    message: '',
  };

  @State() invoiceEmail: string = '';
  @State() acceptedFileType: string[] = ['pdf', 'ofd', 'jpg', 'jpeg', 'png', 'gif'];

  @State() activeTab: InvoiceTypeEnum.ENTERPRISE_INVOICE | InvoiceTypeEnum.ALL_INVOICE = InvoiceTypeEnum.ENTERPRISE_INVOICE;
  @State() selectStatus: SelectStatusEnum.SELECTE_ALL | SelectStatusEnum.SELECTE_NONE = SelectStatusEnum.SELECTE_NONE;

  @State() pageNum: number = 1;
  @State() pageSize: number = 10;
  @State() totalNum: number = 0;
  @State() invoices: InvoiceItem[] = [];

  @State() showDetail: boolean = false;
  @State() currentInvoiceDetail: InvoiceDetailData = null;

  @Watch('visible')
  visibleChanged(newValue: boolean) {
    if (newValue) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
  }

  /**
   * Open the invoice selector drawer
   */
  @Method()
  async open() {
    this.visible = true;
    this.showDetail = false;
    this.getInvoiceList();
    this.getInvoiceEmail();
  }

  /**
   * Close the invoice selector drawer
   */
  @Method()
  async close() {
    this.visible = false;
    this.invoices = [];
    this.onCancel.emit();
  }

  private handleClose() {
    this.close();
  }

  private handleMaskClick() {
    this.close();
  }

  /**
   * 网络错误 刷新列表，专属邮箱
   */
  private onRefresh() {
    this.pageNum = 1;
    this.getInvoiceList();
    this.getInvoiceEmail();
  }

  /**
   * 获取发票列表
   */
  private async getInvoiceList() {
    this.isError = false;
    try {
      const headers = safeParse(this.customheader);
      const customqueryparam = safeParse(this.customqueryparam);
      const params = {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        queryScene: this.activeTab,
        encryptData: this.token,
        appId: this.appid,
        ...customqueryparam,
      };

      const { data } = (await http(api.invoiceList, this.forward, headers, params)) as ApiResponse<InvoiceItem[]>;

      this.totalNum = data.totalNum;

      // 已经关联 未关联
      const linkedInvoiceArr = JSON.parse(sessionStorage.linkedInvoiceArr || '[]');

      this.invoices = data.items.map(item => {
        // 未报销
        if (item.reimburseStatus === ReimburseStatusEnum.REIMBURSE_WAIT) {
          if (linkedInvoiceArr.includes(item.invoiceNo)) {
            item.linked = true;
          }
          item.selected = false;
        }
        return { ...item };
      });

      // 重置全选框
      this.selectStatus = SelectStatusEnum.SELECTE_NONE;
      this.scrollToTop();
    } catch (error) {
      this.invoices = [];
      this.isError = true;
      this.onError.emit(error);
    }
  }

  /**
   * 获取专属邮箱
   */
  private async getInvoiceEmail() {
    if (this.invoiceEmail) return;

    try {
      const heasers = safeParse(this.customheader);
      const customqueryparam = safeParse(this.customqueryparam);
      const params = {
        encryptData: this.token,
        appId: this.appid,
        ...customqueryparam,
      };

      const { data } = (await http(api.invoiceEmail, this.forward, heasers, params)) as ApiResponse<InvoiceItem[]>;

      this.invoiceEmail = data.emailAddress || '';
    } catch (e) {
      console.log('获取邮箱失败', e);
    }
  }

  /**
   * 上传发票
   */
  private async handleUpload() {
    // 创建文件输入元素
    const input = document.createElement('input');
    input.type = 'file';
    input.accept = this.acceptedFileType.map(ext => `.${ext}`).join(',');
    input.style.display = 'none';

    // 添加文件选择事件监听
    input.onchange = async (event: Event) => {
      const target = event.target as HTMLInputElement;
      const file = target.files?.[0];

      if (!file) {
        return;
      }

      // 检查文件大小（10MB = 10 * 1024 * 1024 bytes）
      const maxSize = 10 * 1024 * 1024;
      if (file.size > maxSize) {
        this.showMessage('error', '文件大小不能超过10MB');
        return;
      }

      // 检查文件类型
      const fileType = file.name.split('.').pop()?.toLowerCase();
      if (!this.acceptedFileType.includes(fileType)) {
        input.value = '';
        this.showMessage('error', '只支持PDF和OFD格式的文件');
        return;
      }
      this.showLoading = true;

      const heasers = safeParse(this.customheader);
      const customqueryparam = safeParse(this.customqueryparam);
      const formData = objectToFormData({
        encryptData: this.token,
        appId: this.appid,
        fileType: fileType,
        ...customqueryparam,
      });
      formData.append('data', file);

      try {
        (await http(api.invoiceUpload, this.forward, heasers, formData)) as ApiResponse<InvoiceItem[]>;
        this.onRefresh();
        // 上传成功
        this.showLoading = false;
        this.showMessage('success', '上传成功');
      } catch (e) {
        console.error('上传失败:', e);
        this.showLoading = false;
        this.showMessage('error', e.message || '文件上传失败，请重试');
      } finally {
        document.body.removeChild(input);
      }
    };

    // 将input添加到body并触发点击
    document.body.appendChild(input);
    input.click();
  }

  /**
   * 滑动到顶部
   */
  private scrollToTop() {
    const scrollContainer = this.el.shadowRoot.querySelector('.invoice-table');
    if (scrollContainer) {
      scrollContainer.scrollTo({
        top: 0,
        behavior: 'smooth', // 平滑滚动
      });
    }
  }

  /**
   * 切换发票类型
   */
  private setActiveTab(tab: InvoiceTypeEnum.ENTERPRISE_INVOICE | InvoiceTypeEnum.ALL_INVOICE) {
    this.activeTab = tab;
    this.pageNum = 1;
    this.totalNum = 0;
    this.getInvoiceList();
  }
  /**
   * 发票选择
   * 单选-点击
   * 多选-点击
   */
  private toggleInvoiceSelection(invoice: InvoiceItem) {
    if (invoice.reimburseStatus === ReimburseStatusEnum.REIMBURSE_FINISH) return;
    if (this.multiple) {
      // 多选
      this.invoices = this.invoices.map(item => (item.invoiceNo === invoice.invoiceNo ? { ...item, selected: !item.selected } : item));
      // 判断是否需要设置全选
      let allSelected = true;
      for (const invoice of this.invoices) {
        if (invoice.reimburseStatus === ReimburseStatusEnum.REIMBURSE_WAIT && !invoice.selected) {
          allSelected = false;
          break;
        }
      }
      this.selectStatus = allSelected ? SelectStatusEnum.SELECTE_ALL : SelectStatusEnum.SELECTE_NONE;
    } else {
      // 单选
      const linkedInvoiceArr = [invoice.invoiceNo];
      sessionStorage.linkedInvoiceArr = JSON.stringify(linkedInvoiceArr);
      this.onConfirm.emit([invoice]);
      this.close();
    }
  }

  /**
   * 全选
   * 取消全选
   */
  private handleSelectAll() {
    if (this.selectStatus === SelectStatusEnum.SELECTE_ALL) {
      // 取消全选 去掉已关联的发票
      this.invoices = this.invoices.map(invoice => {
        if (invoice.reimburseStatus === ReimburseStatusEnum.REIMBURSE_WAIT) {
          return {
            ...invoice,
            selected: false,
          };
        } else {
          return invoice;
        }
      });
      this.selectStatus = SelectStatusEnum.SELECTE_NONE;
    } else {
      // 全选 去掉已关联的发票
      this.invoices = this.invoices.map(invoice => {
        if (invoice.reimburseStatus === ReimburseStatusEnum.REIMBURSE_WAIT) {
          return {
            ...invoice,
            selected: true,
          };
        } else {
          return invoice;
        }
      });
      this.selectStatus = SelectStatusEnum.SELECTE_ALL;
    }
  }

  /**
   * 发票预览
   */
  private onPreviewInvoice(invoice) {
    if (invoice.imageUrl) {
      window.open(invoice.imageUrl, '_blank');
    } else {
      this.showMessage('error', '该发票为纸票，暂无预览图');
    }
  }

  /**
   * 获取已选发票数量
   */
  private getSelectedCount(): number {
    return this.invoices.filter(invoice => invoice.selected).length;
  }

  /**
   * 上一页
   */

  private prevPage() {
    if (this.pageNum === 1) return;
    this.pageNum--;
    this.getInvoiceList();
  }

  /**
   * 下一页
   */
  private nextPage() {
    if (this.pageNum === this.totalNum) return;
    this.pageNum++;
    this.getInvoiceList();
  }

  /**
   * 确定
   */
  private handleConfirm() {
    // 将选中的发票存起来 已关联未报销状态
    const linkedInvoiceArr = this.invoices.filter(invoice => invoice.selected).map(invoice => invoice.invoiceNo);
    sessionStorage.linkedInvoiceArr = JSON.stringify(linkedInvoiceArr);
    // 返回选中发发票信息
    const selectedInvoices = this.invoices.filter(invoice => invoice.selected);
    this.onConfirm.emit(selectedInvoices);
    this.close();
  }

  /**
   * 取消
   */
  private handleCancel() {
    this.invoices = this.invoices.map(invoice => ({ ...invoice, selected: false }));
    this.close();
  }

  /**
   * 显示message
   */

  private showMessage(type: '' | 'success' | 'error', message: string) {
    this.messageSetting = { type, message };
    setTimeout(() => {
      this.messageSetting = { type: '', message: '' };
    }, 1500);
  }

  /**
   * 关闭发票详情
   */
  private handleDetailClose() {
    this.showDetail = false;
  }

  render() {
    return (
      <Host class={this.visible ? 'visible' : ''}>
        {this.visible && <div class="invoice-selector-mask" onClick={() => this.handleMaskClick()}></div>}
        {this.showLoading && <fullscreen-loading message="上传中..." />}

        <div class="invoice-selector-drawer">
          {this.messageSetting.type && <my-message type={this.messageSetting.type} message={this.messageSetting.message}></my-message>}
          <div class="message"></div> {/* 消息容器 */}
          <div class="invoice-selector-header">
            <div class="header-title">
              <h2>选择发票</h2>
              {this.invoiceEmail && (
                <a href="#" class="efficiency-tip">
                  用专属收票邮箱<span>{this.invoiceEmail}</span>
                </a>
              )}
            </div>
            <div class="header-close" onClick={() => this.handleClose()}>
              <span>×</span>
            </div>
          </div>
          {this.isError ? (
            <div class="error-con">
              <img class="error-icon" src={errorIcon} />
              <div class="error-message">网络异常，请稍后重试</div>
              <div class="error-refresh" onClick={() => this.onRefresh()}>
                立即刷新
              </div>
            </div>
          ) : (
            <div class="invoice-table-con">
              <div class="invoice-selector-actions">
                <div class="upload-button" onClick={() => this.handleUpload()}>
                  上传发票
                </div>
                <div class="tab-switcher">
                  <div
                    class={this.activeTab === InvoiceTypeEnum.ENTERPRISE_INVOICE ? 'tab-button active' : 'tab-button'}
                    onClick={() => this.setActiveTab(InvoiceTypeEnum.ENTERPRISE_INVOICE)}
                  >
                    企业
                  </div>
                  <div class={this.activeTab === InvoiceTypeEnum.ALL_INVOICE ? 'tab-button active' : 'tab-button'} onClick={() => this.setActiveTab(InvoiceTypeEnum.ALL_INVOICE)}>
                    全部
                  </div>
                </div>
              </div>
              <div class="invoice-table">
                <div class="table-header">
                  <div class="table-cell checkbox-cell" onClick={() => this.handleSelectAll()}>
                    {this.multiple &&
                      (this.selectStatus === SelectStatusEnum.SELECTE_ALL ? (
                        <img class="selected-icon" src={selectedIcon}></img>
                      ) : (
                        <img class="selected-icon" src={unselectedIcon}></img>
                      ))}
                  </div>
                  <div class="table-cell">开票日期</div>
                  <div class="table-cell">销方名称</div>
                  <div class="table-cell">发票金额</div>
                  <div class="table-cell">购方名称</div>
                  <div class="table-cell">销售类型</div>
                  <div class="table-cell">操作</div>
                </div>

                {this.invoices.length === 0 ? (
                  <div class="empty-state">
                    <img class="empty-icon" src={emptyIcon} />
                    <div class="empty-message">暂无数据/内容</div>
                  </div>
                ) : (
                  this.invoices.map(invoice => (
                    <div
                      class={{
                        'table-row': true,
                        'linked': invoice.linked,
                        'selected': invoice.selected,
                      }}
                      key={invoice.invoiceNo}
                      onClick={() => this.toggleInvoiceSelection(invoice)}
                    >
                      <div class="table-cell checkbox-cell">
                        {invoice.reimburseStatus === ReimburseStatusEnum.REIMBURSE_FINISH ? (
                          <img class="relation-icon" src={relationIcon}></img>
                        ) : (
                          this.multiple && (invoice.selected ? <img class="selected-icon" src={selectedIcon}></img> : <img class="selected-icon" src={unselectedIcon}></img>)
                        )}
                      </div>
                      <div class="table-cell">{invoice.invoiceDate}</div>
                      <div class="table-cell">{invoice.payeeName}</div>
                      <div class="table-cell">{invoice.invoiceAmount}</div>
                      <div class="table-cell">{invoice.payerName}</div>
                      <div class="table-cell">{invoice.invoiceLabel}</div>
                      <div class="table-cell">
                        <div
                          class="view-link"
                          onClick={e => {
                            e.stopPropagation();
                            this.onPreviewInvoice(invoice);
                          }}
                        >
                          查看发票
                        </div>
                      </div>
                      {invoice.linked && <img class="not-reimbursed-tips" src={notReimbursedTips}></img>}
                      {invoice.reimburseStatus === ReimburseStatusEnum.REIMBURSE_FINISH && <img class="reimbursed-tips" src={reimbursedTips}></img>}
                    </div>
                  ))
                )}
                {this.totalNum > 0 && (
                  <div class="pagination-con">
                    <img
                      class={{
                        'left-con': true,
                        'gary': this.pageNum === 1,
                      }}
                      onClick={() => this.prevPage()}
                      src={leftArrow}
                    ></img>
                    <div class="current-page-con">{this.pageNum}</div>
                    <img class="slash" src={slash}></img>
                    <div class="total-page-con">{this.totalNum}</div>
                    <img
                      class={{
                        'right-con': true,
                        'gary': this.pageNum === this.totalNum,
                      }}
                      onClick={() => this.nextPage()}
                      src={rightArrow}
                    ></img>
                  </div>
                )}
              </div>

              <div class="invoice-selector-footer">
                {this.multiple ? <div class="selected-count">已选发票：{this.getSelectedCount()} 张</div> : <></>}

                <div class="footer-actions">
                  <button class="cancel-button" onClick={() => this.handleCancel()}>
                    取消
                  </button>
                  {this.multiple && (
                    <button class="confirm-button" onClick={() => this.handleConfirm()}>
                      确定
                    </button>
                  )}
                </div>
              </div>
            </div>
          )}
        </div>
        <invoice-detail visible={this.showDetail} invoiceData={this.currentInvoiceDetail} onCloseDetail={() => this.handleDetailClose()}></invoice-detail>
      </Host>
    );
  }
}
